import React, { useState, useEffect } from 'react';
import AppwriteService from './api/AppwriteService';
import './crud.css'; // Importing external CSS file

const appwriteService = new AppwriteService();

const CrudPage = () => {
    const [documents, setDocuments] = useState([]);
    const [formData, setFormData] = useState({ docId: '', data: {} });
    const [newDocData, setNewDocData] = useState({ highscore: 0 });
    const [message, setMessage] = useState(null);
    const [filters, setFilters] = useState({ minScore: 0, maxScore: 100 });

    useEffect(() => {
        fetchDocuments();
    }, [filters]);

    const fetchDocuments = async () => {
        try {
            const response = await appwriteService.listDocuments([
                `highscore>=${filters.minScore}`,
                `highscore<=${filters.maxScore}`
            ], 100);
            setDocuments(response.documents);
        } catch (error) {
            console.error('Error fetching documents:', error);
            setMessage({ type: 'error', text: 'Error fetching documents.' });
        }
    };

    const handleCreate = async () => {
        try {
            await appwriteService.createDocument(formData.docId, newDocData);
            setMessage({ type: 'success', text: 'Document created successfully.' });
            fetchDocuments();
        } catch (error) {
            console.error('Error creating document:', error);
            setMessage({ type: 'error', text: 'Error creating document.' });
        }
    };

    const handleUpdate = async (docId) => {
        try {
            await appwriteService.updateDocument(docId, formData.data);
            setMessage({ type: 'success', text: 'Document updated successfully.' });
            fetchDocuments();
        } catch (error) {
            console.error('Error updating document:', error);
            setMessage({ type: 'error', text: 'Error updating document.' });
        }
    };

    const handleDelete = async (docId) => {
        try {
            await appwriteService.deleteDocument(docId);
            setMessage({ type: 'success', text: 'Document deleted successfully.' });
            fetchDocuments();
        } catch (error) {
            console.error('Error deleting document:', error);
            setMessage({ type: 'error', text: 'Error deleting document.' });
        }
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const handleNewDocChange = (e) => {
        const { name, value } = e.target;
        setNewDocData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const handleFilterChange = (e) => {
        const { name, value } = e.target;
        setFilters((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const getTopUsers = (count) => {
        return documents
            .sort((a, b) => b.highscore - a.highscore)
            .slice(0, count);
    };

    return (
        <div className="container">
            <header className="header">
                <h1>CRUD Operations</h1>
            </header>
            <div className="content">
                {message && <div className={`message ${message.type}`}>{message.text}</div>}
                <section className="section">
                    <h2>Create Document</h2>
                    <input
                        type="text"
                        name="docId"
                        placeholder="Document ID"
                        value={formData.docId}
                        onChange={handleChange}
                    />
                    <input
                        type="number"
                        name="highscore"
                        placeholder="Highscore"
                        value={newDocData.highscore}
                        onChange={handleNewDocChange}
                    />
                    <button onClick={handleCreate}>Create</button>
                </section>

                <div className="filter-container">
                    <h2>Filter Documents</h2>
                    <input
                        type="number"
                        name="minScore"
                        placeholder="Min Score"
                        value={filters.minScore}
                        onChange={handleFilterChange}
                    />
                    <input
                        type="number"
                        name="maxScore"
                        placeholder="Max Score"
                        value={filters.maxScore}
                        onChange={handleFilterChange}
                    />
                    <button onClick={fetchDocuments}>Apply Filters</button>
                </div>

                <div className="stat-container">
                    <div className="stat-section">
                        <h3>Champion User</h3>
                        <ul className="document-list">
                            {getTopUsers(1).map((doc) => (
                                <li key={doc.$id} className="document-item">
                                    <p><strong>ID:</strong> {doc.$id}</p>
                                    <p><strong>Highscore:</strong> {doc.highscore}</p>
                                </li>
                            ))}
                        </ul>
                    </div>

                    <div className="stat-section">
                        <h3>Top 3 Users</h3>
                        <ul className="document-list">
                            {getTopUsers(3).map((doc) => (
                                <li key={doc.$id} className="document-item">
                                    <p><strong>ID:</strong> {doc.$id}</p>
                                    <p><strong>Highscore:</strong> {doc.highscore}</p>
                                </li>
                            ))}
                        </ul>
                    </div>

                    <div className="stat-section">
                        <h3>Top 10 Users</h3>
                        <ul className="document-list">
                            {getTopUsers(10).map((doc) => (
                                <li key={doc.$id} className="document-item">
                                    <p><strong>ID:</strong> {doc.$id}</p>
                                    <p><strong>Highscore:</strong> {doc.highscore}</p>
                                </li>
                            ))}
                        </ul>
                    </div>

                    <div className="stat-section">
                        <h3>Top 100 Users</h3>
                        <ul className="document-list">
                            {getTopUsers(100).map((doc) => (
                                <li key={doc.$id} className="document-item">
                                    <p><strong>ID:</strong> {doc.$id}</p>
                                    <p><strong>Highscore:</strong> {doc.highscore}</p>
                                </li>
                            ))}
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default CrudPage;
