// BottomNavBar.js
import React, { useState, useEffect, useCallback } from 'react';
import { BrowserRouter as Router, Route, Routes, useNavigate } from 'react-router-dom';

function BottomNavBar() {
  const navigate = useNavigate();

  const handleNavigation = (path) => {
    navigate(path);
  };

  return (
    <div className="bottomNav">
      {/* <button className="actionButton" onClick={() => handleNavigation('/tap')}> */}
        {/* <i className="fas fa-hand-pointer"></i> Tap icon */}
        {/* Tap */}
      {/* </button> */}
     

      <button className="actionButton" onClick={() => handleNavigation('/competition')}>
        <span role="img" aria-label="trophy"></span> {/* Trophy emoji */}
        Competition
      </button>

      <button className="actionButton" onClick={() => handleNavigation('/')}>
        <span role="img" aria-label="bird"></span> {/* Bird emoji */}
        Fly
      </button>
      <button className="actionButton" onClick={() => handleNavigation('/referral')}>
        <i className="fas fa-share"></i> {/* Referral icon */}
        Referral
      </button>
            {/* Add more buttons with icons and text labels here */}
    </div>
  );
}

export default BottomNavBar;
