import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import obstacle1 from '../assets/obstacle1.png';
import obstacle2 from '../assets/obstacle2.png';
import obstacle3 from '../assets/obstacle3.png';
import obstacle4 from '../assets/obstacle4.png';
import obstacle5 from '../assets/obstacle5.png';
import obstacle6 from '../assets/obstacle6.png';
import obstacle7 from '../assets/obstacle7.png';
import obstacle8 from '../assets/obstacle8.png';
import obstacle9 from '../assets/obstacle9.png';
import obstacle10 from '../assets/obstacle10.png';

const images = [
  obstacle1,
  obstacle2,
  obstacle3,
  obstacle4,
  obstacle5,
  obstacle6,
  obstacle7,
  obstacle8,
  obstacle9,
  obstacle10,
];

const Obstacle = ({ position, collided }) => {
    const [imageIndex, setImageIndex] = useState(10);

    useEffect(() => {
        const randomIndex = Math.floor(Math.random() * images.length);
        setImageIndex(randomIndex);

        return () => {
            setImageIndex(10);
        };
    }, []);

    const currentImage = images[imageIndex];
    const imageSize = collided ? 300 : 50; // Change size to 300 if collided
    const zIndex = collided ? 3 : 1; // Higher zIndex when collided

    return (
        <div
            className={`obstacle-wrapper ${collided ? 'obstacle-collided' : ''}`}
            style={{ position: 'absolute', left: collided ? 130 :  position.x, top: collided ? 100 :position.y, zIndex: zIndex }}
        >
            <img
                src={currentImage}
                alt="obstacle"
                className="obstacle"
                style={{
                    width: `${imageSize}px`,
                    height: `${imageSize}px`,
                }}
            />
        </div>
    );
};

Obstacle.propTypes = {
    position: PropTypes.shape({
        x: PropTypes.number.isRequired,
        y: PropTypes.number.isRequired,
    }).isRequired,
    collided: PropTypes.bool.isRequired,
};

export default Obstacle;
