import React, { useState, useEffect } from 'react';
import Bird from './components/Bird';
import Obstacle from './components/Obstacle';
import Confetti from 'react-dom-confetti';
import './Pigeon.css';

import UserIcon from './components/UserIcon';
import UserProfile from './UserProfile';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import { jumpSound, collisionSound } from './sounds/snd';
import { Howl } from 'howler';
import AppwriteService from './api/AppwriteService';
const appwriteService = new AppwriteService();
const Pigeon = () => {
    const [birdPosition, setBirdPosition] = useState({ x: 50, y: 200 });
    const [obstacles, setObstacles] = useState([]);
    const [gameOver, setGameOver] = useState(false);
    const [score, setScore] = useState(0);
     const [highScore, setHighScore] = useState(0);
    // const [highScore, setHighScore] = useState(() => {
    //     const storedHighScore = localStorage.getItem('highScore');
    //     return storedHighScore ? parseInt(storedHighScore) : 0;
    // });
    const [gameStarted, setGameStarted] = useState(false);
    const [showConfetti, setShowConfetti] = useState(false);
    const [startScore, setStartScore] = useState(0);
    const [countdown, setCountdown] = useState(0); // Add countdown state
    const [showResumeButton, setShowResumeButton] = useState(false); // Add state for resume button
    const [showHighScoreMessage, setShowHighScoreMessage] = useState(false);
    const [user, setUser] = useState(null);

    const [documents, setDocuments] = useState([]);
    const [message, setMessage] = useState(null);
    const [filters, setFilters] = useState({ minScore: 0, maxScore: 100 });

    const GRAVITY = 2;
    const JUMP_HEIGHT = 50;
    const OBSTACLE_WIDTH = 40;
    const OBSTACLE_HEIGHT = 40;
    const OBSTACLE_SPEED = 5;
    const OBSTACLE_GENERATION_INTERVAL = 600;
    
    const fetchDocuments = async () => {
        try {
            const response = await appwriteService.listDocuments([
                `highscore>=${filters.minScore}`,
                `highscore<=${filters.maxScore}`
            ], 100);
            setDocuments(response.documents);
        } catch (error) {
            console.error('Error fetching documents:', error);
            setMessage({ type: 'error', text: 'Error fetching documents.' });
        }
    };

    const getTopUsers = (count) => {
        return documents
            .sort((a, b) => b.highscore - a.highscore)
            .slice(0, count);
    };

    useEffect(() => {
        fetchDocuments();
    }, [filters]);

    const createOrUpdateDocument = async (newHighScore) => {
        if (!user) return;

        const userId = String(user.id).trim();
        const highScoreStr = String(newHighScore).substring(0, 25);

        try {
            const existingDoc = await appwriteService.getDocument(userId);
            if (existingDoc) {
                if (newHighScore > parseInt(existingDoc.highscore)) {
                    await appwriteService.updateDocument(userId, { highscore: highScoreStr });
                    setMessage({ type: 'success', text: 'Document updated successfully.' });
                }
            } else {
                await appwriteService.createDocument(userId, { highscore: highScoreStr, username: user.username, first_name: user.first_name, last_name: user.last_name,  });

                setMessage({ type: 'success', text: 'Document created successfully.' });
            }
            fetchDocuments();
        } catch (error) {
            if (error.code === 404) {
                try {
                    await appwriteService.createDocument(userId, { highscore: highScoreStr, username: user.username, first_name: user.first_name, last_name: user.last_name,  });

                    setMessage({ type: 'success', text: 'Document created successfully.' });
                } catch (createError) {
                    console.error('Error creating document:', createError);
                    setMessage({ type: 'error', text: `Error creating document: ${createError.message}` });
                }
            } else {
                console.error('Error updating document:', error);
                setMessage({ type: 'error', text: `Error updating document: ${error.message}` });
            }
        }
    };

    useEffect(() => {
        const tg = window.Telegram.WebApp;
        tg.ready();

        const initDataUnsafe = tg.initDataUnsafe;
        const user = initDataUnsafe?.user;

        if (user) {
            setUser(user);
            const storedHighScore = localStorage.getItem('highScore');
            const userId = String(user.id).trim();

            appwriteService.getDocument(userId)
                .then(doc => {
                    if (doc) {
                        const serverHighScore = parseInt(doc.highscore);
                        const localHighScore = storedHighScore ? parseInt(storedHighScore) : 0;
                        const highestScore = Math.max(serverHighScore, localHighScore);

                        setHighScore(highestScore);

                        if (highestScore > serverHighScore) {
                            createOrUpdateDocument(highestScore);
                        }

                        localStorage.setItem('highScore', highestScore);
                    } else if (storedHighScore) {
                        setHighScore(parseInt(storedHighScore));
                    }
                })
                .catch(error => {
                    if (error.code !== 404) {
                        console.error('Error fetching document:', error);
                    } else if (storedHighScore) {
                        setHighScore(parseInt(storedHighScore));
                    }
                });
        }
    }, []);

    useEffect(() => {
        if (user) {
            const storedHighScore = localStorage.getItem('highScore');
            const localHighScore = storedHighScore ? parseInt(storedHighScore) : 0;

            if (highScore > localHighScore) {
                localStorage.setItem('highScore', highScore);
                createOrUpdateDocument(highScore);
            }
            if (localHighScore > highScore) {
                
                createOrUpdateDocument(localHighScore);
            }
        }
    }, [user, highScore]);


    const jump = () => {
        if (countdown > 0 || gameOver || showResumeButton) return; // Prevent any action during countdown, if game is over, or waiting for resume button click
        if (!gameOver && gameStarted) {
            setShowHighScoreMessage(false);
            jumpSound.play();
            setBirdPosition((prev) => ({ ...prev, y: prev.y - JUMP_HEIGHT }));
        } else if (!gameOver && !gameStarted) {
            startGame();
        }
    };

    const startGame = () => {
        setShowHighScoreMessage(false);
        setGameStarted(true);
        setGameOver(false);
        setObstacles([]);
        setBirdPosition({ x: 50, y: 200 });
        setShowHighScoreMessage(false);
        

        if (startScore > 0) {
            setScore(startScore);
            setStartScore(0); // Reset startScore after resuming game
        } else {
            setScore(0);
        }
    };

    const restartGame = () => {
        setShowHighScoreMessage(false);
        setGameOver(false);
        setGameStarted(false);
        setObstacles([]);
        setScore(0);
        setBirdPosition({ x: 50, y: 200 });
    };

    const lifeGiven = () => {
        setShowHighScoreMessage(false);
        setStartScore(score);
        setCountdown(9); // Start the countdown
      
    };

    const resumeGame = () => {
        if (countdown === 0 ) {
            setShowResumeButton(false); // Show resume button when countdown ends
            setGameOver(false);
            setObstacles([]);
            setBirdPosition({ x: 50, y: 200 });
            setGameStarted(true);
        }
        
    };

    useEffect(() => {
        if (countdown > 0) {
            const countdownInterval = setInterval(() => {
                setShowResumeButton(true);
                setCountdown(prevCountdown => prevCountdown - 1);
            }, 1000);
            return () => clearInterval(countdownInterval);
        }
    }, [countdown, startScore]);

   

    const checkCollision = () => {
        const birdTop = birdPosition.y;
        const birdBottom = birdPosition.y + 50;
        const birdLeft = birdPosition.x;
        const birdRight = birdPosition.x + 50;

        obstacles.forEach((obstacle, index) => {
            const obstacleTop = obstacle.y;
            const obstacleBottom = obstacle.y + OBSTACLE_HEIGHT;
            const obstacleLeft = obstacle.x;
            const obstacleRight = obstacle.x + OBSTACLE_WIDTH;

            const isColliding =
                birdRight > obstacleLeft &&
                birdLeft < obstacleRight &&
                birdBottom > obstacleTop &&
                birdTop < obstacleBottom;

            if (isColliding) {
                setObstacles(prevObstacles => {
                    const newObstacles = [...prevObstacles];
                    newObstacles[index].collided = true;
                    return newObstacles;
                });
                endGame();
            }

            if (!obstacle.passed && birdLeft > obstacleRight) {
                setScore(prevScore => prevScore + 1);
                setObstacles(prevObstacles => {
                    const newObstacles = [...prevObstacles];
                    newObstacles[index].passed = true;
                    return newObstacles;
                });
            }
        });

        if (birdBottom > 600 || birdTop < 0) {
            endGame();
        }
    };

    const endGame = () => {
        collisionSound.play();
        setGameOver(true);
        setGameStarted(false);

        if (score > highScore) {
            setHighScore(score);
            localStorage.setItem('highScore', score.toString());
            setShowConfetti(true);
            setShowHighScoreMessage(true);
        } else {
            setShowConfetti(false);
        }
    };

    useEffect(() => {
        if (gameStarted) {
            const gravityInterval = setInterval(() => {
                setBirdPosition(prev => ({ ...prev, y: prev.y + GRAVITY }));
            }, 30);

            return () => clearInterval(gravityInterval);
        }
    }, [gameStarted]);

    useEffect(() => {
        const obstacleGeneratorInterval = setInterval(() => {
            if (gameStarted && !gameOver) {
                const randomHeight = Math.floor(Math.random() * 550);
                setObstacles(prev => [...prev, { x: 600, y: randomHeight, passed: false }]);
            }
        }, OBSTACLE_GENERATION_INTERVAL);

        return () => clearInterval(obstacleGeneratorInterval);
    }, [gameStarted, gameOver]);

    useEffect(() => {
        const obstacleMoveInterval = setInterval(() => {
            if (gameStarted && !gameOver) {
                setObstacles(prev =>
                    prev.map(obstacle => ({ ...obstacle, x: obstacle.x - OBSTACLE_SPEED })).filter(obstacle => obstacle.x + OBSTACLE_WIDTH > 0)
                );
            }
        }, 30);

        return () => clearInterval(obstacleMoveInterval);
    }, [gameStarted, gameOver]);

    useEffect(() => {
        if (gameStarted && !gameOver) {
            checkCollision();
        }
    }, [birdPosition, obstacles]);

    const confettiConfig = {
        angle: 90,
        spread: 360,
        startVelocity: 60, // Increase the initial velocity for a more dynamic effect
        elementCount: 2000, // Increase the number of confetti pieces
        dragFriction: 0.1, // Adjust drag to slow down the confetti more gradually
        duration: 6000, // Increase duration to keep confetti visible for longer
        stagger: 5, // Increase stagger for a more continuous effect
        width: "50px", // Make the confetti pieces larger
        height: "50px", // Make the confetti pieces larger
        colors: ["#a864fd", "#29cdff", "#78ff44", "#ff718d", "#fdff6a", "#ffffff", "#ff0000"] // Add more colors for variety
    };
    

    return (
        <div className={`Pigeon ${gameOver ? 'game-over' : ''}`} onClick={jump}>
         
            <Bird birdPosition={birdPosition} />
            {obstacles.map((obstacle, index) => (
                <Obstacle key={index} position={obstacle} collided={obstacle.collided} />
            ))}
            {gameOver && (
                <center>
                    <div className="game-over-message">
                        Game Over!
                        <br />
                        <button className="restart-button" onClick={(e) => { e.stopPropagation(); restartGame(); }}>Restart</button>
                        <button className="give-life-button" onClick={(e) => { e.stopPropagation(); lifeGiven(); }}>Give Life</button>
                    </div>
                </center>
            )}
            
            {showResumeButton && (
            <div className="modal">
                <div className="modal-content">
                <button className="close-button" onClick={resumeGame}>
                    {countdown > 0 ? countdown : 'X'}
                </button>
                <div className="iframe-container">
                    <iframe
                    title="External Content"
                    src={'https://oudsutch.com/4/6605635'}
                    
                    />
                </div>
                </div>
            </div>
            )}

            <div className="score">
                Score: {score}
            </div>
            <div className="high-score">
                High Score: {highScore}
            </div>
            {showHighScoreMessage && (
            <div className="high-score-message">
                Congratulations! New High Score: {highScore}
            </div>
            )}
            <Confetti active={showConfetti} config={confettiConfig} />
        </div>
    );

};

export default Pigeon;
