import React, { useState, useEffect } from 'react';
import Skeleton from 'react-loading-skeleton';
import 'react-loading-skeleton/dist/skeleton.css';
import './Ref.css';

const ReferralPage = () => {
  const [invitationLink, setInvitationLink] = useState('');
  const [loading, setLoading] = useState(true);
  const [copySuccess, setCopySuccess] = useState(false);

  useEffect(() => {
    // Initialize Telegram SDK and fetch user information
    const tg = window.Telegram.WebApp;
    if (tg) {
      tg.ready();

      const initDataUnsafe = tg.initDataUnsafe;
      const user = initDataUnsafe?.user;
      
      if (user) {
        const userId = user.id;
        const link = `http://t.me/Famtap_bot?start=${userId}`;
        setInvitationLink(link);
      }

      setLoading(false); // Finish loading once user data is fetched
    } else {
      console.error('Telegram SDK not initialized');
    }
  }, []);

  const copyToClipboard = () => {
    navigator.clipboard.writeText(invitationLink)
      .then(() => {
        setCopySuccess(true);
        setTimeout(() => setCopySuccess(false), 3000);
      })
      .catch(error => console.error("Failed to copy to clipboard", error));
  };

  return (
    <div className="page-container">
      <div className="page-content">
        <h1 className="page-title">Referral Page</h1>
        <p className="subtitle">Invite your friends and earn rewards!</p>
        <p className="subtitle">You will earn 5000 pts!</p>
        <p className="subtitle">Your friend will earn 2000 pts!</p>
        {loading ? (
          <Skeleton width={200} height={20} />
        ) : (
          <div className="invitation-container">
            <p className="invitation-link">{invitationLink}</p> 
            <button className="copy-button" onClick={copyToClipboard}>Copy</button>
            {copySuccess && <p className="copy-success">Invitation link copied to clipboard!</p>}
          </div>
        )}
        {/* Add more content here, such as referral invite form */}
      </div>
    </div>
  );
};

export default ReferralPage;
