import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './App.css';
import AppwriteService from './api/AppwriteService';

const appwriteService = new AppwriteService();

const UserProfile = () => {
    const navigate = useNavigate();
    const [user, setUser] = useState(null);
    const [highScore, setHighScore] = useState(0);
    const [documents, setDocuments] = useState([]);
    const [message, setMessage] = useState(null);
    const [filters, setFilters] = useState({ minScore: 0, maxScore: 100 });

    const fetchDocuments = async () => {
        try {
            const response = await appwriteService.listDocuments([
                `highscore>=${filters.minScore}`,
                `highscore<=${filters.maxScore}`
            ], 100);
            setDocuments(response.documents);
        } catch (error) {
            console.error('Error fetching documents:', error);
            setMessage({ type: 'error', text: 'Error fetching documents.' });
        }
    };

    const getTopUsers = (count) => {
        return documents
            .sort((a, b) => b.highscore - a.highscore)
            .slice(0, count);
    };

    useEffect(() => {
        fetchDocuments();
    }, [filters]);

    const createOrUpdateDocument = async (newHighScore) => {
        if (!user) return;

        const userId = String(user.id).trim();
        const highScoreStr = String(newHighScore).substring(0, 25);

        try {
            const existingDoc = await appwriteService.getDocument(userId);
            if (existingDoc) {
                if (newHighScore > parseInt(existingDoc.highscore)) {
                    await appwriteService.updateDocument(userId, { highscore: highScoreStr });
                    setMessage({ type: 'success', text: 'Document updated successfully.' });
                }
            } else {
                await appwriteService.createDocument(userId, { highscore: highScoreStr, username: user.username, first_name: user.first_name, last_name: user.last_name,  });
                setMessage({ type: 'success', text: 'Document created successfully.' });
            }
            fetchDocuments();
        } catch (error) {
            if (error.code === 404) {
                try {
                    await appwriteService.createDocument(userId, { highscore: highScoreStr, username: user.username, first_name: user.first_name, last_name: user.last_name,  });

                    setMessage({ type: 'success', text: 'Document created successfully.' });
                } catch (createError) {
                    console.error('Error creating document:', createError);
                    setMessage({ type: 'error', text: `Error creating document: ${createError.message}` });
                }
            } else {
                console.error('Error updating document:', error);
                setMessage({ type: 'error', text: `Error updating document: ${error.message}` });
            }
        }
    };

    useEffect(() => {
        const tg = window.Telegram.WebApp;
        tg.ready();

        const initDataUnsafe = tg.initDataUnsafe;
        const user = initDataUnsafe?.user;

        if (user) {
            setUser(user);
            const storedHighScore = localStorage.getItem('highScore');
            const userId = String(user.id).trim();

            appwriteService.getDocument(userId)
                .then(doc => {
                    if (doc) {
                        const serverHighScore = parseInt(doc.highscore);
                        const localHighScore = storedHighScore ? parseInt(storedHighScore) : 0;
                        const highestScore = Math.max(serverHighScore, localHighScore);

                        setHighScore(highestScore);

                        if (highestScore > serverHighScore) {
                            createOrUpdateDocument(highestScore);
                        }

                        localStorage.setItem('highScore', highestScore);
                    } else if (storedHighScore) {
                        setHighScore(parseInt(storedHighScore));
                    }
                })
                .catch(error => {
                    if (error.code !== 404) {
                        console.error('Error fetching document:', error);
                    } else if (storedHighScore) {
                        setHighScore(parseInt(storedHighScore));
                    }
                });
        }
    }, []);

    useEffect(() => {
        if (user) {
            const storedHighScore = localStorage.getItem('highScore');
            const localHighScore = storedHighScore ? parseInt(storedHighScore) : 0;

            if (highScore > localHighScore) {
                localStorage.setItem('highScore', highScore);
                createOrUpdateDocument(highScore);
            }
            if (localHighScore > highScore) {
                
                createOrUpdateDocument(localHighScore);
            }
        }
    }, [user, highScore]);

    const handleBackClick = () => {
        navigate('/');
    };

    if (!user) {
        return (
            <div style={{ padding: '20px', textAlign: 'center' }}>
                <button className="back-button" onClick={handleBackClick}>Back</button>
                <h1>Loading user information...</h1>
                <p><strong>High Score:</strong> {highScore}</p>
            </div>
        );
    }

    return (
        <div style={{ padding: '20px', textAlign: 'center' }}>
            {/* <button className="back-button" onClick={handleBackClick}>Back</button> */}
            <div className="id">
            <p><strong>Name:</strong> {user.first_name} {user.last_name}</p>
            <p><strong>Your High Score:</strong> {highScore}</p>
            </div>
            <div className="stat-section">
                <h3>Top 3 Users</h3>
                <ul className="document-list">
                    {getTopUsers(3).map((doc) => (
                        <li key={doc.$id} className="document-item">
                            <p><strong>ID:</strong> {doc.username}</p>
                            <p><strong>Highscore:</strong> {doc.highscore}</p>
                        </li>
                    ))}
                </ul>
            </div>
        </div>
    );
};

export default UserProfile;
