import React, { useState, useEffect, useCallback, useContext } from 'react';
import './App.css';
import Skeleton from 'react-loading-skeleton';
import 'react-loading-skeleton/dist/skeleton.css';
import * as Appwrite from 'appwrite';
import { useNavigate } from 'react-router-dom';
import motivationalMessages from './motivationalMessages';
import { UserContext } from './UserContext';

const client = new Appwrite.Client();
const database = new Appwrite.Databases(client);

client
.setEndpoint('https://serverprovider.fulbek.com/v1')
.setProject('6668146b001b27aae761');

const databaseId = '665d31c9001a2bd4c357';
const collectionId = '665d31d20031a6b989b3';

const currencyEmojis = ['', '', '', '', '', '', '', '', '', ''];

function MainPage() {
  const { user } = useContext(UserContext);
  const [coinValue, setCoinValue] = useState(null);
  const [energyLevel, setEnergyLevel] = useState(null);
  const [energy, setEnergy] = useState(null);
  const [foods, setFoods] = useState([
    { name: 'fruits', value: 5 },
    { name: 'vegetables', value: 5 },
    { name: 'meat', value: 5 },
    { name: 'dairy', value: 5 },
  ]);
  const [taps, setTaps] = useState([]);
  const [isTapped, setIsTapped] = useState(false);
  const [error, setError] = useState(null);
  const [newTapCount, setNewTapCount] = useState(0);
  const [motivationalMessage, setMotivationalMessage] = useState(null);
  const [isCelebrating, setIsCelebrating] = useState(false);
  const [celebrationEmojis, setCelebrationEmojis] = useState([]);

  const navigate = useNavigate();

  const triggerCelebration = () => {
    const randomEmojis = Array.from({ length: 5 }, () => currencyEmojis[Math.floor(Math.random() * currencyEmojis.length)]);
    setCelebrationEmojis(randomEmojis);
    setIsCelebrating(true);
    setTimeout(() => setIsCelebrating(false), 3000);
  };

  const getRandomMessage = () => {
    return motivationalMessages[Math.floor(Math.random() * motivationalMessages.length)];
  };

  useEffect(() => {
    setMotivationalMessage(getRandomMessage());
  }, []);

  const fetchUserCoinValue = useCallback(async () => {
    try {
      const document = await database.getDocument(databaseId, collectionId, user.userId);
      setCoinValue(document.coinValue || 0.0);
      setEnergyLevel(document.energyLevel || 100);
      setEnergy(document.energyLevel || 100);
    } catch (error) {
      setError('Come back later');
    }
  }, [user.userId]);

  useEffect(() => {
    if (user.userId) {
      fetchUserCoinValue();
    }
  }, [user.userId, fetchUserCoinValue]);

  useEffect(() => {
    const interval = setInterval(() => {
      if (energy < energyLevel) {
        setEnergy((prevEnergy) => prevEnergy + 1);
      }
    }, 1000);
    return () => clearInterval(interval);
  }, [energy, energyLevel]);

  const incrementCoinValue = async (e) => {
    const rect = e.target.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;

    if (energy > 0) {
      const newCoinValue = coinValue + 1.0;
      setCoinValue(newCoinValue);
      setEnergy((prevEnergy) => prevEnergy - 1);
      setTaps((taps) => [...taps, { x, y }]);
      setIsTapped(true);
      setTimeout(() => setIsTapped(false), 100);

      const updatedTapCount = newTapCount + 1;
      setNewTapCount(updatedTapCount);

      if (updatedTapCount % 10 === 0) {
        triggerCelebration();
      }

      if (updatedTapCount % 12 === 0) {
        setMotivationalMessage(getRandomMessage());
      }

      try {
        await database.updateDocument(databaseId, collectionId, user.userId, { coinValue: newCoinValue });
      } catch (error) {
        console.error('Take some break', error);
        setError('Take some break');
      }
    }
  };
  
  const refAction = () => {
    navigate('/referral');
  };

  const taskAction = () => {
    navigate('/task');
  };

  const tapAction = () => {
    navigate('/');
  };

  const boostAction = () => {};

  const statsAction = () => {};

  const renderActionButton = (title, onClick) => (
    <button className="actionButton" onClick={onClick}>
      {title}
    </button>
  );

  return (
    <>
      <div className="App">
        <div className="userInfo">
          {error && <div className="error">{error}</div>}
          <div className="username">
            {user.username ? user.username : (user.firstname ? user.firstname : user.lastname)}
          </div>
          <div className="coinCount">
            <div className="coinIcon"></div>
            <span className="coinValue">
              {coinValue !== null ? `${coinValue}` : <Skeleton className="skeleton" width={100} />}
            </span>
          </div>
          <div className="legendaryStatus">
            {motivationalMessage || <Skeleton className="skeleton" width={150} />}
          </div>
        </div>
        {isCelebrating && (
          <div className="celebration">
            {celebrationEmojis.map((emoji, index) => (
              <span key={index} className="celebration-emoji">{emoji}</span>
            ))}
          </div>
        )}
        <div className="coinContainer" onClick={incrementCoinValue}>
          <div className={`coin ${isTapped ? 'tapped' : ''}`}>
            {taps.map((tap, index) => (
              <div
                key={index}
                className="plusOne"
                style={{ left: tap.x, top: tap.y }}
                onAnimationEnd={() => setTaps((taps) => taps.filter((_, i) => i !== index))}
              >
                +1
              </div>
            ))}
          </div>
        </div>
        <div className="energyContainer">
          <div className="energyTitle">
            {energy !== null ? 'Energy' : <Skeleton className="skeleton" width={100} />}
          </div>
          <div className="energyBar">
            {energy !== null ? (
              <div className="energyFill" style={{ width: `${(energy / energyLevel) * 100}%` }}></div>
            ) : (
              <Skeleton className="skeleton" height={20} />
            )}
          </div>
          <div className="energyText">
            {energy !== null ? `${energy} / ${energyLevel}` : <Skeleton className="skeleton" width={100} />}
          </div>
        </div>
      </div>
      <div className="bottomNav">
        {renderActionButton('Referral', refAction)}
        {renderActionButton('Tasks', taskAction)}
        {renderActionButton('Tap', tapAction)}
        {renderActionButton('Boost', boostAction)}
        {renderActionButton('Stats', statsAction)}
      </div>
    </>
  );
}

export default MainPage;
